
body {
  margin: 0;
}
.container {
  width: 100%;
  margin: 0 auto;
  background-color: #008080;
  font-family: arial;
  min-height: 100vh;
  position: relative;
}
* {
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.header {
  background-color: rgba(0, 74, 74, 0.9);
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
}
.header h1 {
  /*padding: 15px;*/
  margin: 0;
  color: #FFFFFF;
  font-size: 50px;
}
.content {
  text-align: center;
  padding: 20px;
  margin-top: 55px;
}
.previous, .next {
  padding: 12px 15px;
  top:0;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 30px;
  position:absolute;
}
.middle {
  margin: 0 150px;
  text-align: center;
}
.previous {
  left:0;
}
.next {
  right:0;
}
.clear {
  clear: both;
}
